.footer-contacts{
    @include container();
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: $containerColor;
    border-radius: 40px;
    height: 450px;
    margin-top: 85px;
    &__coffetime{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 530px;
        height: 250px;
        margin-left: 80px;
        
    }
    &__title{
        margin-top: 85px;
        @include heading-2();
        text-align: left;
        color: $textLight;
        &-accent{
            color: $textAccent;
        }
    }
    &__icons{
        display: flex;
        flex-direction: row;
        margin-top: 40px;
        &-item{
            margin-right: 8px;
        }
    }
    &__us{
        width: 530px;
        height: 186px;
        
        &-title{
            margin-top: 115px;
            @include heading-3();
            color: $textLight;
        }
        &-info{
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            width: 262px;
            height: 116px;
            margin-top: 40px;
        }

    }
}