.mobile-app{
    @include container();
    margin-top: 85px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    &__title{
        @include heading-2();
        text-align: left;
        margin-bottom: 35px;
        &-accent{
            color: $textAccent;
        }
    }
    &__info{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 630px;
        height: 342px;
        margin-top: 125px;
        
    }
    &__buttons{
        display: flex;
        flex-direction: row;
        justify-content: left;

    }
    &__button{
        
        margin: 12px 20px 12px 20px;
        background-color: inherit;
    }
    &__screen{
        width: 630px;
        height: 630px;
    }
}