.enjoy{
    @include container();
    height: 644px;
    border-radius: 40px;
    margin-top: 25px;
    &__list{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 530px;
        height: 529px;
        
        margin: 0 0 85px 85px;

        
        &-title{
            margin-top: 85px;
            @include heading-1();
            color: $textLight;
            &__accent{
                color: $textAccent;
            }
        }
        &-description{
            @include medium();
            color: $textLight;
        }
    }
}