$mainWidth: 660px;
$mainHeight: 660px;

.box{
    overflow: hidden;
    width: $mainWidth;
    height: $mainHeight;
    border-radius: 20px;
    &__img{
        width: 100%;
        height: 100%;
        border-radius: 20px;
        transition: all 1s ease-out;
        
        transform: scale(1.1);
    }
    &__img:hover{
        transform: none;
        
    }
}

